@font-face {
  font-family: Georgia;
  src: url('../main/../../assets/fonts/9605.ttf');
}

@font-face {
  font-family: Arial;
  src: url('../main/../../assets/fonts/9041.ttf');
}

/* color constants */
$color-light-xl: #ffffff;
$color-light-l: #f6f6f6;
$color-light-s: #fafafa;
$color-dark-s: #cdcdcd;
$color-dark-m: #b2b2b2;
$color-dark-l: #545454;
$color-dark-xl: #4c4c4c;
$color-dark-2xl: #444444;
$color-dark-3xl: #292929;
$color-primary: #f1cdb3;
$color-primary-light: #fddcc4;
$start-screen-gradient-background: #211f20;
$footer-gradient-background: #211f20;

// Mixin
@mixin h2 {
  font: {
    family: Georgia;
    size: 4.4rem;
    weight: 400;
  }
  line-height: 130%;
}

@mixin h3 {
  font: {
    family: Georgia;
    style: normal;
    size: 3.5rem;
    weight: normal;
  }
  line-height: 130%;
  letter-spacing: 0.06rem;
}

@mixin h4 {
  font: {
    family: Georgia;
    style: normal;
    size: 2rem;
    weight: normal;
  }
  line-height: 115%;
  letter-spacing: 0.06rem;
}

@mixin h5 {
  font: {
    family: Georgia;
    style: normal;
    size: 1.5rem;
    weight: normal;
  }
  line-height: 110%;
  letter-spacing: 0.06rem;
}

@mixin paragraph-l {
  font: {
    family: Arial;
    style: normal;
    size: 1.5rem;
    weight: normal;
  }
  line-height: 160%;
}

@mixin paragraph-s {
  font: {
    family: Arial;
    style: italic;
    size: 1.2rem;
    weight: normal;
  }
  line-height: 150%;
}

@mixin logo-title {
  font: {
    family: Arial;
    style: normal;
    size: 3.2rem;
    weight: normal;
  }
  line-height: 110%;
  letter-spacing: 0.06rem;
}

@mixin logo-subtitle {
  font: {
    family: Arial;
    style: normal;
    size: 1.3rem;
    weight: normal;
  }
  line-height: 110%;
  letter-spacing: 0.06rem;
}

:root {
  font-size: 10px;
}

// * normalize
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  position: relative;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1200px;
}

/* Header */
.header {
  width: 100%;
  position: absolute;
  top: 60px;
}

.logo {
  display: flex;
  flex-direction: column;

  &__title {
    color: $color-primary;
    font-family: Georgia;
    font-size: 32px;
    font-weight: 400;
    letter-spacing: 1.92px;
    line-height: 35.2px;
  }

  &__subtitle {
    color: $color-light-xl;
    font-family: Arial;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: 1.3px;
    margin: 11px 0 0 5px;
  }
}

.menu {
  width: 443px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;

  &__item {
    display: flex;
    align-items: center;
  }

  &-link {
    align-items: center;
    color: $color-dark-s;
    font-family: Arial;
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    text-decoration: none;
  }

  &__item--active {
    color: $color-light-s;
    text-decoration: none;
    border-bottom: 2px solid#f1cdb3;
  }
}

/* Main */
.main {
  display: flex;
  flex-direction: column;
}

.hero {
  display: flex;
  padding-top: 180px;
  background: url(../../assets/images/noise_transparent@2x.png),
    radial-gradient(100% 215.42% at 0% 0%, #5b483a 0%, #262425 100%), #211f20;

  &__content {
    display: flex;
  }

  section-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 85px;
  }

  &__title {
    max-width: 310px;
    height: 114px;
    color: $color-light-xl;
    font-family: Georgia;
    font-size: 44px;
    font-weight: 400;
    line-height: 57.2px;
    margin-bottom: 42px;
  }

  &__text {
    width: 460px;
    color: $color-dark-s;
    font-family: Arial;
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 41px;
  }
  &__img {
    margin-left: 40px;
  }

  .btn {
    width: 207px;
    height: 52px;
    border-radius: 100px;
    background-color: $color-primary;
    color: $color-dark-3xl;
    font-family: Georgia;
    font-size: 17px;
    font-weight: 400;
    letter-spacing: 1.02px;
    line-height: 22.1px;
    cursor: pointer;
    outline: none;
  }
}
